.screen-big-event {
	@include image('screen-big-event-bg.png');
	background-repeat: no-repeat;
	background-position: right bottom;
	.container {
		display: flex;
		flex-wrap: wrap; }

	&__block-gallery,
	&__info {
		box-sizing: border-box;
		padding: 0 15px;
		width: 50%;
		@include r(700) {
			width: 100%; } } }

.block-gallery {
	@include r(700) {
		margin-bottom: 40px; }
	&__wrapper {
		margin-bottom: 24px;
		display: flex;
		flex-wrap: wrap;
		@include r(700) {
			margin-bottom: 20px; }
		@supports (display: grid) {
			display: grid;
			grid-gap: 0;
			grid-template-columns: 1fr 1fr 1fr; } }
	&__col {
		text-align: center;
		overflow: hidden;
		width: calc(100% / 3);
		@supports (display: grid) {
			width: 100%; }
		&_66 {
			width: calc(100% - (100% / 3));
			@supports (display: grid) {
				width: 100%;
				grid-column: 1 / 3;
				@include r(1050) {
					grid-column: span 3; }
				@include r(700) {
					grid-column: span 2; }
				@include r(500) {
					grid-column: span 3; } } }
		&_33 {
			&:nth-of-type(2) {
				@supports (display: grid) {
					@include r(1050) {
						grid-row: span 3;
						grid-column: span 2; }
					@include r(700) {
						grid-row: span 1;
						grid-column: span 1; }
					@include r(500) {
						grid-row: span 3;
						grid-column: span 2; } } } } }

	&__img {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		height: 100%;
		object-fit: cover; } }

.block-gallery-soc {
	&__text {
		margin-bottom: 12px;
		font-family: $got;
		font-weight: 700;
		@include fs(16);
		@include lh(16, 24);
		color: rgba($blue, .6);
		@include r(700) {
			text-align: center; } }
	&__list {
		@include r(700) {
			justify-content: center;
			.soc-list-li:last-of-type {
				margin-right: 0; } } } }

.info {
	display: flex;
	flex-direction: column;
	text-align: center;
	&__subtitle {
		font-family: $got;
		font-weight: 700;
		@include fs(13);
		@include lh(13, 24);
		color: rgba($blue, .6); }
	&__title {
		margin-bottom: 48px;
		font-family: $const;
		font-weight: 700;
		font-style: italic;
		@include fs(27);
		color: $pink;
		@include r(1200) {
			margin-bottom: 40px;
			@include fs(25); }
		@include r(870) {
			// margin-bottom: 20px
			@include fs(22); }
		@include r(800) {
			@include fs(20); }
		@include r(700) {
			margin-bottom: 30px; }
		&_margin {
			margin-top: auto; } }
	&-list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		&-li {
			margin-bottom: 24px;
			box-sizing: border-box;
			padding: 0 12px;
			width: 50%;
			@include r(1050) {
				margin-bottom: 68px;
				width: 100%; }
			@include r(920) {
				margin-bottom: 50px; }
			@include r(870) {
				margin-bottom: 30px; }
			@include r(750) {
				margin-bottom: 20px; }
			@include r(700) {
				margin-bottom: 25px; }
			&__title,
			&__text {
				margin-bottom: 6px;
				@include fs(14);
				@include lh(14, 20);
				font-weight: 700;
				@include r(1050) {
					@include fs(16); }
				@include r(900) {
					@include fs(15); }
				@include r(800) {
					@include fs(14); } }
			&__text {
				margin-bottom: 0;
				color: rgba($blue, .7); } } }
	.btn-wrapper {
		margin: 0 auto 0; }
	.btn {
		padding: 0 65px;
		@include r(800) {
			padding: 0 15px; } } }
