.screen-about {
	&__wrapper {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around; }
	&__worker {
		margin: 0 3%;
		width: 23.6%;
		@include r(900) {
			margin: 0 15px;
			width: calc((100% - 90px) / 3); }
		@include r(800) {
			margin: 0 10px;
			width: calc((100% - 60px) / 3); }
		@include r(700) {
			width: calc((100% - 30px) / 3);
			margin: 0 5px; }
		@include r(650) {
			width: calc((100% - 20px) / 2); }
		@include r(400) {
			margin: 0;
			width: 100%; }
		@include r(400) {
			margin-bottom: 30px; } } }


.worker {
	&-img {
		width: 100%;
		height: 260px;
		overflow: hidden;
		text-align: center;
		&__img {
			display: inline-block;
			vertical-align: middle;
			width: auto;
			height: 100%;
			@supports (object-fit: cover) {
				width: 100%;
				object-fit: cover; } } }
	&-info {
		box-sizing: border-box;
		padding: 20px 15px 30px;
		text-align: center;
		background-color: rgba($orange, .3);
		@include r(700) {
			padding: 15px 10px 20px; }
		@include r(600) {
			padding: 10px 5px 15px; }
		&__name {
			margin-bottom: 10px;
			@include fs(18);
			font-weight: 700;
			@include r(700) {
				margin-bottom: 5px; }
			@include r(600) {
				@include fs(16); }
			@include r(600) {
				@include fs(15); } }
		&__text {
			margin: 0;
			@include fs(15);
			color: rgba($blue, .7); } }
	&:nth-of-type(1),
	&:nth-of-type(2),
	&:nth-of-type(3) {
		margin-bottom: 100px;
		@include r(900) {
			margin-bottom: 50px; }
		@include r(800) {
			margin-bottom: 30px; }
		.worker-info {
			background-color: rgba($pink, .3); } } }
