.page-reviews {
	padding: 100px 0 115px;
	@include image('page-reviews-bg.jpg');
	background-position: top right;
	background-repeat: no-repeat;
	background-size: 100% auto;
	@include r(1200) {
		padding: 90px 0 105px; }
	@include r(1000) {
		padding: 80px 0 90px; }
	@include r(800) {
		padding: 70px 0 80px; }
	@include r(600) {
		padding: 60px 0 70px; }
	&__title {
		margin: 0 auto 35px;
		text-align: center;
		font-family: $const;
		@include fs(27);
		font-weight: 700;
		font-style: italic;
		color: $blue;
		@include r(900) {
			margin-bottom: 30px;
			@include fs(25); }
		@include r(600) {
			margin-bottom: 25px;
			@include fs(22); }
		@include r(400) {
			margin-bottom: 20px;
			@include fs(20); } }

	.btn-wrapper {
		margin-bottom: 40px;
		@include r(1000) {
			margin-bottom: 35px; }
		@include r(800) {
			margin-bottom: 30px; }
		@include r(600) {
			margin-bottom: 25px; }
		@include r(400) {
			margin-bottom: 20px; } }

	&-select {
		margin: 0 auto 80px;
		text-align: center;
		@include r(1000) {
			margin-bottom: 70px; }
		@include r(800) {
			margin-bottom: 60px; }
		@include r(600) {
			margin-bottom: 50px; }
		@include r(400) {
			margin-bottom: 40px; }
		&__text {
			display: inline-block;
			vertical-align: middle;
			margin-right: 20px;
			margin-left: -95px;
			@include fs(18);
			@include r(660) {
				margin-left: auto;
				@include fs(14); }
			@include r(500) {
				margin-right: 0;
				margin-bottom: 10px;
				width: 100%; } }
		&__select {
			display: inline-block;
			vertical-align: middle;
			box-sizing: border-box;
			width: 390px;
			max-width: 100%;
			text-align: left;
			@include fs(18);
			@include r(660) {
				@include fs(14); } } } }

.reviews-list {
	margin: 0 auto;
	width: 100%;
	max-width: 754px;
	&-li {
		display: block;
		margin-bottom: 60px;
		&.is-hide {
			display: none; } } }

.review {
	&-header {
		display: flex;
		align-items: flex-start;
		margin-bottom: 20px;
		box-sizing: border-box;
		padding: 15px 40px 45px 20px;
		@include image('review-header-bg.jpg');
		background-size: 100% 100%;
		transition: .25s;
		@include r(660) {
			margin-bottom: 10px;
			padding: 15px 15px 40px 15px; }
		@include r(550) {
			flex-wrap: wrap;
			background-position: top center;
			background-size: 320% 100%; }
		&__category {
			margin-right: 20px;
			width: 154px;
			font-family: $const;
			@include fs(18);
			font-weight: 700;
			font-style: italic;
			@include r(660) {
				@include fs(16); }
			@include r(550) {
				width: 100%;
				margin-right: 0;
				margin-bottom: 10px; } }
		&-address {
			align-self: flex-end;
			font-style: normal;
			@include r(380) {
				margin-bottom: 10px; }
			&__name {
				display: block;
				margin-bottom: 5px;
				@include fs(18);
				font-weight: 700;
				color: $pink;
				@include r(660) {
					margin-bottom: 0;
					@include fs(16); } }
			&__date {
				@include fs(14);
				color: rgba($blue, .6); } }

		&-like {
			margin-left: auto;
			margin-right: 0;
			@include r(380) {
				margin-l: 0;
				margin-right: auto;
				width: 100%; }
			&_liked {
				.review-header-like__icon path {
					fill: $pink; } }
			&__icon {
				display: inline-block;
				vertical-align: middle;
				box-sizing: border-box; }
			&__text {
				display: inline-block;
				vertical-align: middle;
				@include fs(18);
				font-weight: 700;
				color: rgba($blue, .6);
				@include r(660) {
					@include fs(15); } } } }

	&-content {
		box-sizing: border-box;
		padding: 0 20px;
		@include fs(17);
		@include lh(17, 28);
		@include r(660) {
			padding: 0 15px;
			@include fs(15); } } }
