.overlay {
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100vh;
	overflow: auto;
	background-color: rgba($blue, .7);
	opacity: 0;
	transition: .25s;
	&.is-active {
		opacity: 1;
		z-index: 100; }
	&__close {
		position: absolute;
		top: 5px;
		right: 5px;
		width: 30px;
		height: 30px;
		font-size: 0;
		cursor: pointer;
		border: 0;
		background-color: transparent;
		outline: none;
		// &::before,
		// &::after
		// 	content: ''
		// 	position: absolute
		// 	width: 30px
		// 	height: 3px
		// 	background-color: $pink
		// 	transition: .25s
		// &::before
		// 	top: 50%
		// 	left: 0
		// 	transform: rotate(45deg)
		// &::after
		// 	top: 50%
		// 	left: 0
		// 	transform: rotate(-45deg)
		// &:hover::before,
		// &:hover::after
 }		// 	background-color: $orange
	&-content {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 50px 0;
		width: 100%;
		min-height: 100vh;
		box-sizing: border-box;
		&-form {
			position: relative;
			display: none;
			flex-direction: column;
			padding: 22px 40px 40px;
			width: 444px;
			max-width: calc(100% - 30px);
			box-sizing: border-box;
			background-color: #fff;
			opacity: 0;
			transition: .25s;
			@include r(1200) {
				padding: 22px 35px 40px; }
			@include r(1000) {
				padding: 22px 30px 35px; }
			@include r(600) {
				padding: 22px 25px 30px; }
			@include r(400) {
				padding: 22px 20px 20px; }
			&.is-active {
				display: flex;
				opacity: 1; }
			&__title {
				margin-bottom: 25px;
				text-align: center;
				font-family: $const;
				@include fs(20);
				@include lh(20, 27);
				font-weight: 600;
				color: $blue;
				@include r(1200) {
					@include fs(24); }
				@include r(1000) {
					@include fs(22); }
				@include r(800) {
					@include fs(20); }
				@include r(600) {
					@include fs(18); }
				@include r(400) {
					@include fs(16); } }

			&-label {
				display: block;
				margin-bottom: 20px;
				width: 100%;
				&:last-of-type {
					margin-bottom: 40px; }
				&_check:last-of-type {
					margin-bottom: 20px; }

				&__text {
					display: block;
					margin-bottom: 10px;
					@include fs(14);
					font-weight: 700; }
				&__input {
					display: block;
					box-sizing: border-box;
					padding: 6px 10px;
					width: 100%;
					height: 39px;
					@include fs(16);
					border: 2px solid $pink;
					transition: .25s;
					&:hover {
						border-color: $blue; }
					&:focus {
						border-color: $orange; }
					&_textarea {
						height: 120px;
						resize: none; }
					&.input-date {
						display: none; } }

				.datepicker-inline .datepicker {
					width: 100%;
					border-color: $pink;
					border-width: 2px;
					border-radius: 0; }
				.datepicker--cell.-selected-,
				.datepicker--cell.-selected- {
					background-color: $pink;
					transition: .25s;
					&.-current- {
						background-color: $pink; }
					&.-focus- {
						background-color: $orange; } }
				.datepicker--cell.-current-, .datepicker--cell.-current-.-in-range- {
					color: $pink; }

				&__recommend-pseudo-input path {
					transition: .25s; }
				&_check {
					display: flex;
					justify-content: center;
					align-items: center;
					cursor: pointer;
					&:hover .overlay-content-form-label__recommend-pseudo-input path {
						fill: rgba($pink, .5); } }
				&__recommend-input {
					display: none;
					&:checked + .overlay-content-form-label__recommend-pseudo-input path {
						fill: $pink; } }
				&__recommend-text {
					display: inline-block;
					margin-bottom: 5px;
					margin-left: 5px;
					@include fs(16);
					font-weight: 700; } }

			.btn {
				align-self: center;
				& + .overlay-content-form__title {
					margin-top: 20px;
					margin-bottom: 0; } } } } }
