.screen {
	padding: 74px 0 116px;
	@include r(1200) {
		padding: 64px 0 96px; }
	@include r(920) {
		pading: 54px 0 76px; }
	@include r(800) {
		padding: 40px 0 60px; }
	&__title {
		margin: 0 auto 50px;
		text-align: center;
		font-family: $const;
		@include fs(27);
		letter-spacing: 0.04em;
		font-weight: 700;
		font-style: italic;
		color: $blue;
		@include r(1200) {
			margin-bottom: 40px;
			@include fs(25); }
		@include r(920) {
			margin-bottom: 30px;
			@include fs(23); }
		@include r(800) {
			margin-bottom: 20px;
			@include fs(21); } }
	&__wrapper {
		display: flex;
		flex-wrap: wrap;
		&_around {
			justify-content: space-around; }
		&_center {
			justify-content: center; } } }
