.footer {
	margin-bottom: 0;
	padding: 20px 0 40px;
	background-color: $blue;
	&-link {
		display: inline-block;
		vertical-align: top;
		font-family: $got;
		@include fs(14);
		color: #fff;
		text-decoration: none;
		transition: .25s;
		&:hover {
			color: $orange;
			text-decoration: none; } }
	.container {
		display: flex;
		flex-wrap: wrap;
 }		// flex-direction: column
	&-logo {
		width: 25%;
		@include r(850) {
			width: 30%; }
		@include r(700) {
			margin-bottom: 35px;
			width: 50%; }
		@include r(500) {
			width: 100%;
			text-align: center; }
		&-link {
			text-decoration: none;
			&__img {
				max-width: 185px;
				width: 100%;
				height: auto; } } }
	&-contacts {
		width: 25%;
		@include r(850) {
			width: 30%; }
		@include r(700) {
			margin-bottom: 35px;
			width: 50%; }
		@include r(500) {
			width: 100%;
			text-align: center; }
		.footer-link {
			margin-bottom: 20px; } }
	&-address {
		display: block;
		font-style: normal;
		color: #fff; }
	&-nav {
		width: 50%;
		@include r(850) {
			width: 40%; }
		@include r(700) {
			margin-bottom: 35px;
			width: 100%; }
		&-list {
			text-align: right;
			&-li {
				margin-bottom: 12px;
				display: block;
				@include r(700) {
					width: 100%;
					text-align: center; }
				&:last-of-type {
					margin-bottom: 0; }
				&:nth-of-type(1),
				&:nth-of-type(2),
				&:nth-of-type(3) {
					min-width: 185px;
					display: inline-block;
					@include r(1200) {
						min-width: 150px; }
					@include r(920) {
						min-width: 135px; }
					@include r(850) {
						width: 100%; } } } } }
	&-copy {
		width: 25%;
		align-self: flex-end;
		@include r(850) {
			width: 30%; }
		@include r(700) {
			order: 10;
			width: 100%;
			text-align: center; } }

	.soc-list {
		@include r(700) {
			margin-bottom: 35px;
			justify-content: center;
			width: 100%; } } }
