.screen-lid {
	position: relative;
	padding: 60px 0 70px;
	@include image('lid-1.jpg');
	@include cover;
	background-position: center center;
	@include r(900) {
		padding: 50px 0 60px; }
	@include r(800) {
		padding: 40px 0 50px; }
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($blue, .4); }
	.container {
		position: relative;
		z-index: 2;
		text-align: center; }
	&__text {
		font-family: $const;
		@include fs(27);
		font-weight: 700;
		font-style: italic;
		color: #fff;
		@include r(1200) {
			@include fs(25); }
		@include r(900) {
			@include fs(23); }
		@include r(800) {
			@include fs(21); }
		&_top {
			margin-bottom: 48px;
			@include r(1200) {
				margin-bottom: 35px; }
			@include r(1100) {
				margin-bottom: 30px; }
			@include r(900) {
				margin-bottom: 25px; }
			@include r(800) {
				margin-bottom: 20px; } } }
	.btn {
		margin-bottom: 48px;
		@include fs(18);
		margin-bottom: 48px;
		@include r(1200) {
			margin-bottom: 35px; }
		@include r(1100) {
			margin-bottom: 30px; }
		@include r(900) {
			margin-bottom: 25px; }
		@include r(800) {
			margin-bottom: 20px; }
		&:hover {
			color: #fff; } } }
